header
    height: clamp(488px, 120vw , 831px)
    background: top center url("../../assets/header-img.jpg")
    display: flex
    background-size: auto clamp(488px, 120vw , 831px)
    flex-direction: column

.title
    color: white
    max-width: 768px
    height: clamp(114px, 30vw , 315px)
    margin: clamp(52px, 12vw , 119px) auto 0 auto
    text-align: center

.text
    color: #fff
    margin: clamp(40px, 11vw, 50px) auto 0 auto
    text-align: center
    max-width: 562px

.button
    margin: clamp(40px, 11vw, 50px) auto 0 auto
    border: none
    padding: 5px 20px
    background-color: #fff

@media screen and (max-width: 1024px)
    .text
        max-width: clamp(310px, 85vw, 442px)
        padding: 0 20px

@media screen and (max-width:360px)
    .button
        height: 33px

@media (hover: hover)
    .button
        &:hover
            background-color: #EFA501
        &:active
            background-color: #F8E6BE
