.container
    height: 531px
    width: 370px
    position: relative
    border-radius: 45px
    padding: 50px
    cursor: pointer
    overflow: hidden
    transition: all .3s ease
    background-position: center
    background-size: cover

@media (hover: hover)
    .container:hover
        transform: scale(1.2)
        z-index: 1
        padding: 40px
        .button
            background-color: #286562

.name
    color: #fff

.cost
    margin-top: 16px
    color: #fff

.button
    position: absolute
    bottom: 43px
    left: 30px

@media screen and (max-width: 1280px)
    .container
        width: clamp(318px, 46vw, 450px)
        height: clamp(318px, 52vw, 531px)
        margin: 0 auto

@media screen and (max-width: 690px)
    .container
        width: clamp(318px, 90vw, 450px)
        height: clamp(318px, 90vw, 531px)

@media screen and (max-width: 360px)
    .container
        height: 318px
