.container
    display: flex
    flex-direction: column
    max-width: 500px
    min-width: 278px
    width: 100%

.selectContainer
    position: relative
    width: 100%
    height: clamp(43px, 7vw, 68px)

.select
    width: 100%
    height: clamp(43px, 7vw, 68px)
    appearance: none
    padding: 0 20px
    margin-top: 8px
    border: 1px solid #f4f0e9
    background-color: #f9f7f4
    cursor: pointer
    border-radius: 10px
    background-repeat: no-repeat
    background-size: inherit
    background-position: calc(100% - 24px) center

.default
    color: #a6a6a6

@media screen and (max-width: 690px)
    .container
        margin: 0 auto

@media (hover:hover)
    .select:hover
        background-color: #f4f0e9
