.pannel
    padding: 0
    display: flex
    align-items: center
    height: 88px
    margin: 0 auto

nav
    margin: 0 calc(min(10vw, 181px))
    display: flex

.logo
    position: relative
    bottom: 3px

.element
    &:not(:first-child)
        margin-left: 50px

.element
    color: #fff
    cursor: pointer
    border-bottom: 1px solid transparent
    &:hover
        border-bottom-color: #fff

.phone
    color: #fff

@media screen and (max-width: 1024px)
    nav
        margin: 0 clamp(20px, 8vw, 82px)
    .logo
        width: clamp(133px, 36.9vw , 182px)
    .element:not(:first-child)
        margin-left: clamp(10px, 3vw, 30px)

@media screen and (max-width: 800px)
    .element
        display: none
        margin: 0
    nav
        margin: 0
    .pannel
        margin: 0
        padding: 0 clamp(20px, 4vw, 100px)
        justify-content: space-evenly

@media screen and (max-width: 360px)
    .pannel
        padding: 0 calc(min(5.558vw, 20px))
        justify-content: space-between
