.checkbox
    position: absolute
    z-index: -1
    opacity: 0

    &:checked+.label::before
        border-color: #1B1F2B
        background-color: transparent
        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%231B1F2B' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e")

.label
    display: flex
    user-select: none
    cursor: pointer

    &::before
        content: ''
        display: block
        width: 1em
        height: 1em
        flex-grow: 0
        flex-shrink: 0
        border-radius: 0.25em
        margin-right: 0.5em
        background-repeat: no-repeat
        background-position: center center
        background-size: 50% 50%
