.photos
    margin: 0 auto
    display: grid
    grid-template-columns: 1fr
    gap: clamp(18px, 3vw, 30px)

    img
        border-radius: clamp(20px, 5vw, 50px)
        width: 100%

.fullLine
    display: grid
    grid-template-columns: repeat(4, 1fr)
    gap: clamp(20px, 1.5625vw, 30px)

.miniLine
    display: grid
    grid-template-columns: repeat(5, 1fr)
    gap: clamp(20px, 1.5625vw, 30px)

.full
    height: 300px
    max-width: clamp(348px, 23vw, 442px)

.mini
    max-width: clamp(231px, 18.125vw, 348px)

@media screen and (max-width: 1024px)
    .fullLine
        grid-template-columns: repeat(3, 1fr)
        gap: clamp(18px, 1.5625vw, 20px)

    .miniLine
        grid-template-columns: repeat(4, 1fr)
        gap: clamp(10px, 1.5625vw, 30px)

    .table
        display: none

@media screen and (max-width: 480px)
    .fullLine
        grid-template-columns: repeat(2, 1fr)
        gap: 18px

    .miniLine
        grid-template-columns: repeat(3, 1fr)
        gap: 10px

    .mobile
        display: none
