.button {
  border: none;
  background-color: transparent;
  padding: 7px 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-radius: 50px;
  -webkit-transition: background-color 0.5s;
  transition: background-color 0.5s;
}

.buttonText {
  color: #fff;
}

.vector {
  margin-left: 16px;
}
