.footer
    background-color: #F4F0E9

.socials
    height: clamp(85px, 8.6vw, 88px)
    display: flex
    max-width: 1170px
    width: 100%
    margin: 0 auto
    align-items: center
    justify-content: space-between

.greyText
    line-height: 23px
    color: #a6a6a6

.logos
    display: flex

.logo
    display: flex
    align-items: center
    &:not(:first-child)
        margin-left: clamp(19px, 2.5vw, 25px)

.logoImage
    --size: clamp(11px, 1.4vw, 14px)
    width: var(--size)
    height: var(--size)

.logoText
    margin-left: 10px
    border-bottom: 1px solid transparent

@media screen and (max-width: 1024px)
    .socials
        max-width: 918px
        padding: 0 40px

@media screen and (max-width: 690px)
    .socials
        flex-direction: column
        justify-content: start
        padding: 21px 0 16px 0
    .logos
        margin-top: 7px

@media screen and (max-width: 360px)
    .socials
        max-width: none
