.container
    display: flex
    flex-direction: column
    max-width: 500px
    min-width: 278px
    width: 100%
    cursor: pointer

.input
    margin-top: 8px
    background-color: #f9f7f4
    border: 1px solid #f4f0e9
    line-height: clamp(43px, 7vw, 68px)
    border-radius: 10px
    padding: 0 20px
    font-size: clamp(16px, 2vw, 20px)
    &::placeholder
        font-weight: 400
        font-size: clamp(16px, 2vw, 20px)

@media (hover: hover)
    .input
        &:hover
            background-color: #f4f0e9
            cursor: pointer
        &:focus
            background-color: #f4f0e9
            border-color: #a6a6a6

@media screen and (max-width: 690px)
    .container
        margin: 0 auto
