.container
    background-color: #F4F0E9
    border-radius: clamp(20px, 4.9vw, 50px)
    padding: clamp(20px, 4vw, 40px)
    position: relative

.text
    white-space: pre-wrap
    margin-bottom: clamp(115px, 15vw, 145px)

.footer
    position: absolute
    bottom: clamp(20px, 4vw, 40px)
    width: calc(100% - 2 * clamp(20px, 4vw, 40px))
    height: 75px

.greyText
    margin-top: 8px
    color: #A6A6A6

.photo
    --size: 75px
    width: var(--size)
    height: var(--size)
    position: absolute
    border-radius: 50%
    bottom: 0
    right: 0
