.travelTime
    display: flex
    align-items: center
    margin: clamp(75px, 14vw, 135px) auto
    max-width: clamp(291px, 75vw, 764px)

.photo
    --size: clamp(133px, 37vw, 370px)
    width: var(--size)
    height: var(--size)
    border-radius: clamp(20px, 4.9vw, 50px)

.text
    display: flex
    flex-direction: column
    margin-left: 30px
    margin-top: 30px

.title
    margin-bottom: 20px
    text-align: center

.email
    text-align: center

.greyText
    color: #a6a6a6

@media screen and (max-width: 690px)
    .title
        letter-spacing: 0.04em
    .travelTime
        flex-direction: column
    .text
        margin-left: 0

@media (hover: hover)
    .link:active
        color: #286562
