.container
    margin-top: clamp(20px, 4.9vw, 50px)
    height: clamp(455px, 55.4vw, 567px)
    position: relative
    overflow: hidden
    border-radius: clamp(20px, 4.9vw, 50px)
    padding: clamp(30px, 5.9vw, 60px)
    transition: all .3s ease
    background-position: center
    background-size: cover

@media (hover: hover)
    .container
        &:hover
            transform: scale(1.2)
            .button
                background-color: #286562

.title
    color: #fff

.text
    margin-top: clamp(20px, 4vw, 37px)
    color: #fff
    max-width: 554px

.advantages
    margin-top: 20px
    padding-left: 30px

.advantage
    color: #fff

.button
    position: absolute
    bottom: clamp(30px, 5.9vw, 60px)
    left: clamp(30px, 5.9vw, 60px)

.links
    list-style: none
    display: flex
    position: absolute
    bottom: clamp(30px, 5.9vw, 60px)
    right: clamp(30px, 5.9vw, 60px)

.link
    color: #fff
    &:not(:first-child)
        margin-left: 30px

@media screen and (max-width: 690px)
    .links
        display: none

@media (hover: hover)
    .link:active
        color: #EFA501
